<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome'  }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品参数</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区 -->
    <el-card>
      <!-- 注意 -->
      <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" show-icon></el-alert>
      <!-- 商品分类 -->
      <el-row class="cat_opt">
        <el-col>
          <span>请选择商品分类：</span>
          <el-cascader
            v-model="selectedKeys"
            :options="catelist"
            :props="cateProps"
            @change="CateChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- 参数与属性 -->
      <el-tabs v-model="activeName" @tab-click="tabClick">
        <!-- 参数 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button :disabled="isBtnDisabled" type="primary" @click="clickAddMany()">添加参数</el-button>
          <!-- 动态参数表格 -->
          <el-table :data="manyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  closable
                  :key="i"
                  v-for="(item ,i) in scope.row.attr_vals"
                  @close="handleClose(i,scope.row)"
                >{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="参数名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row)"
                >修改</el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 属性 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button :disabled="isBtnDisabled" type="primary" @click="clickAddOnly()">添加属性</el-button>
          <!-- 静态参数表格 -->
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  closable
                  :key="i"
                  v-for="(item ,i) in scope.row.attr_vals"
                  @close="handleClose(i,scope.row)"
                >{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="属性名称名称"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row)"
                >修改</el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加动态参数对话框 -->
    <el-dialog title="添加动态参数" :visible.sync="addManyDialog" width="50%" @close="closeMany">
      <el-form :model="addManyRuleForm" :rules="addManyRule" ref="manyRuleForm" label-width="100px">
        <el-form-item label="动态参数" prop="name">
          <el-input v-model="addManyRuleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addManyDialog = false">取 消</el-button>
        <el-button type="primary" @click="clickMany">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改参数对话框 -->
    <el-dialog
      :title="'修改'+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 添加静态属性对话框 -->
    <el-dialog title="添加静态属性" :visible.sync="addOnlyDialog" width="50%">
      <el-form :model="addManyRuleForm" :rules="addOnlyRule" ref="onlyRuleForm" label-width="100px">
        <el-form-item label="静态属性" prop="name">
          <el-input v-model="addOnlyRuleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addOnlyDialog = false">取 消</el-button>
        <el-button type="primary" @click="clickOnly">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Params',
  data() {
    return {
      //商品分类列表
      catelist: [],
      //级联选择框双向绑定到的数组
      selectedKeys: [],
      //级联选择框的配置对象
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
      },
      //活跃的参数名字
      activeName: 'many',
      //动态参数请求出来的数据
      manyTableData: [],
      //静态参数请求出来的数据
      onlyTableData: [],
      //添加动态参数对话框的显示与隐藏
      addManyDialog: false,
      //添加静态属性对话框的显示与隐藏
      addOnlyDialog: false,

      //修改对话框的显示与隐藏
      editDialogVisible: false,
      //修改的表单数据对象
      editForm: {},
      //修改表单验证对象
      editFormRules: {
        attr_name: [
          { required: true, message: '请输入参数', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
      },

      //动态参数表单的双向绑定
      addManyRuleForm: {
        name: '',
      },
      //静态属性表单的双向绑定
      addOnlyRuleForm: {
        name: '',
      },
      //添加动态参数规则
      addManyRule: {
        name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
      },
      //添加静态属性规则
      addOnlyRule: {
        name: [
          { required: true, message: '请输入属性名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
      },
      //展开行
      dynamicTags: ['标签一', '标签二', '标签三'],
      //控制按钮与文本框的切换显示
      inputVisible: false,
      //文本框的输入的内容
      inputValue: '',
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      console.log(res)
      if (res.meta.status !== 200) {
        return this.message.error('发生了未知错误！')
      }
      this.catelist = res.data
      console.log(this.catelist)
    },
    //级联选择框触发的函数
    CateChange() {
      this.getParamsData()
    },
    //监听参数栏切换
    tabClick() {
      this.getParamsData()
    },
    //获取参数列表数据
    async getParamsData() {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      //证明选中的是三级分类
      console.log(this.selectedKeys)
      //根据所选分类的id，和当前所处的面板，获取对应的参数
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName,
          },
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('发生了未知错误！')
      }
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        //控制文本框的显示与隐藏
        item.inputVisible = false
        //输入的值
        item.inputValue = ''
      })
      console.log(res.data)

      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    //点击添加动态参数
    clickAddMany() {
      this.addManyDialog = true
    },
    //点击添加静态属性
    clickAddOnly() {
      this.addOnlyDialog = true
    },
    //点击添加动态参数的确定按钮
    clickMany() {
      this.$refs.manyRuleForm.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.selectedKeys[2]}/attributes`,
          {
            attr_name: this.addManyRuleForm.name,
            attr_sel: this.activeName,
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('发生了未知错误！')
        }
        this.getParamsData()
        this.addManyDialog = false
        return this.$message.success('添加成功！')
      })
    },
    //点击添加静态属性的确定按钮
    clickOnly() {
      this.$refs.manyRuleForm.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.selectedKeys[2]}/attributes`,
          {
            attr_name: this.addOnlyRuleForm.name,
            attr_sel: this.activeName,
          }
        )
        console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('发生了未知错误！')
        }
        this.getParamsData()
        this.addOnlyDialog = false
        return this.$message.success('添加成功！')
      })
    },
    /* 监听动态参数关闭 */
    closeMany() {
      this.$refs.manyRuleForm.resetFields()
    },
    /* 监听静态属性关闭 */
    closeOnly() {
      this.$refs.onlyRuleForm.resetFields()
    },
    //监听修改对话框的关闭
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    //点击按钮，展示修改对话框
    showEditDialog(row) {
      this.editForm = row
      this.editDialogVisible = true
    },
    //修改参数对话框确定按钮
    editParams() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        console.log(this.editForm)
        const {
          data: res,
        } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('发生了未知错误！')
        }
        this.$message.success('修改成功！')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    //删除参数请求
    async deleteParams(attrid) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((error) => error)
      console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        this.$message.info('已取消删除！')
      }

      //删除的业务逻辑
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attrid}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除参数失败！')
      }
      this.getParamsData()
      return this.$message.success('删除成功！')
    },
    //文本框失去焦点，或按下了enter键触发
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        return
      }
      //如果没有return需要后续处理
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      //需要发送请求保存
      this.saveAttrVals(row)
    },

    //将对attr_vals 的操作，保存到数据库
    async saveAttrVals(row) {
      //需要发送请求保存
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' '),
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('发生了未知错误！,更新失败！')
      }
      this.$message.success('更新成功！')
    },
    //点击按钮，展示文本输入框
    showInput(row) {
      row.inputVisible = true
      //让文本框自动获得焦点
      //$nextTick方法作用，当页面上的元素重新渲染后，才会指定回调函数中的代码
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //删除
    handleClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    },
  },
  computed: {
    //如果按钮需要被禁用，则返回true
    isBtnDisabled() {
      if (this.selectedKeys.length !== 3) {
        return true
      }
      return false
    },
    //选中的三级分类的id
    cateId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    //Many显示动态参数
    titleText() {
      if (this.activeName === 'many') {
        return '动态参数'
      }
      return '静态属性'
    },
  },
}
</script>

<style scoped>
.cat_opt {
  margin: 16px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>